// Matches sizes in _sizes.scss
const sizes = {
  xxs: 320,
  xs: 479,
  s: 610,
  m: 768,
  l: 820,
  xl: 990,
  xxl: 1280,
};

const imageElementClass = 'c-masthead--image';

export default class Masthead {
  constructor(element, options) {
    this.element = element;
    this.options = options;
    this.init();
    this.setImage(window.innerWidth);
  }

  init() {
    // turn vertical scrolling off for body
    document.body.style.overflowX = 'hidden';

    this.imageElement = this.element.getElementsByClassName(imageElementClass);
    this.imageSources = [...this.element.querySelectorAll('source')]
      .map(element => {
        return {
          size: element.getAttribute('data-size'),
          src: element.getAttribute('srcset')}
      })
      .sort((a, b) => {
        if(sizes[a.size] === sizes[b.size]) {
          return 0;
        }
        return sizes[a.size] > sizes[b.size] ? -1 : 1;
      });

    window.addEventListener('resize', () => {
      this.setImage(window.innerWidth);
    });

    this.checkEditMode();
  }

  checkEditMode() {
    // Disabled until implementation details are known
    // if(nn.isCmsModeEdit()) {
    //   if(!this.cmsEdit) {
    //     // Lazy load the cms edit mode module
    //     import(/* webpackChunkName: "cmsedit" */ './masthead.cmsedit.js').then((MastheadCmsEdit) => {
    //       this.cmsEdit = new MastheadCmsEdit.default(this.element, this.imageSources, this.imageElement[0]);
    //     });
    //   } else {
    //     this.cmsEdit.start();
    //   }
    // } else if (this.cmsEdit) {
    //   // stop it only if it has been loaded before
    //   this.cmsEdit.stop();
    // }
  }

  setImage(windowWidth) {
    if(this.imageSources.length === 0 || this.imageElement.length === 0) {
      return; // no sources or no image element, do nothing
    }

    if(windowWidth <= sizes.s) {
      // never show an image below mobile breakpoint
      this.imageElement[0].style.backgroundImage = null;
      return;
    }

    const imageSource = this.getImageMatch(windowWidth);
    this.imageElement[0].style.backgroundImage = `url("${imageSource.src}")`;
  }

  getImageMatch(windowWidth) {
    // start with largest
    let bestMatch = this.imageSources[0];

    for(let i = 0; i < this.imageSources.length; i++) {
      if(sizes[this.imageSources[i].size] >= windowWidth) {
        bestMatch = this.imageSources[i];
      } else {
        // stop searching if we're below windowWidth
        return bestMatch;
      }
    }

    return bestMatch;
  }
}
