import Vue from "vue";
import VueComponents from '@nn/vue-components';
import { mapActions, mapGetters } from 'vuex';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';
import { store } from "./store.js";
import './components';

import '../app-vue-shared/shared.js';
import template from './faqCategoryList.html';
import httpService from './http/httpService.js';

import Meta from 'vue-meta';
import queryParamsMixin from "./queryParamsMixin";

Vue.use(Meta);
Vue.use(VueComponents);

const canonicalLinkElement = document.querySelector('link[rel=canonical]');
const canonicalLinkOriginalUrl = canonicalLinkElement ? canonicalLinkElement.getAttribute('href') : '';

function getOrCreateMetaElement(property) {
  let element = document.querySelector('meta[property="' + property + '"]');
  if (!element) {
    element = document.createElement('meta');
    element.setAttribute('property', property);
  }
  return element;
}

export default function faqCategoryList(root, config) {
  httpService.config(config);

  return new Vue({
    el: createVueChildContainer(root),
    mixins: [queryParamsMixin],
    metaInfo() {
      return {
        title: this.selectedMenuCategory.name && this.selectedFaqCategory.name ? this.selectedFaqCategory.name + ' - ' + this.selectedMenuCategory.name : '',
        titleTemplate: chunk => chunk ? chunk + ' - Veelgestelde vragen : Nationale-Nederlanden' : 'Veelgestelde vragen : Nationale-Nederlanden',
        meta: [
          {
            name: 'description',
            content: this.selectedFaqCategory ? 'Antwoorden op veelgestelde vragen over ' + this.selectedFaqCategory.name + ' vindt u hier. Meer weten? Neem gerust contact met ons op.' : 'Veelgestelde vragen : Nationale-Nederlanden'
          }
        ]
      }
    },
    template,
    store,
    data: {
      loading: false,
      classificationId: config.classificationId,
      canonicalLinkBase: ''
    },
    created() {
      if (canonicalLinkElement) {
        this.updateCanonicalLink();
        window.addEventListener("hashchange", this.updateCanonicalLink, false);
      }
    },
    beforeDestroy() {
      window.removeEventListener("hashchange", this.updateCanonicalLink, false);
    },
    mounted() {
      this.loading = true;
      this.getCategories(config)
        .then(() => {
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    watch: {
      selectedMenuCategory() {
        this.setOgData();
      },
      selectedFaqCategory() {
        this.setOgData();
      }
    },
    computed: {
      ...mapGetters([
        'getMenuCategories',
        'getCategoryById'
      ]),
      noResults() {
        return this.categoryRows.length === 0;
      },
      selectedMenuCategory() {
        return this.getCategoryById(this.classificationId, this.selectedMenuCategoryId)
      },
      selectedFaqCategory() {
        return this.getCategoryById(this.classificationId, this.selectedFaqCategoryId)
      },
      categoryRows() {
        // In order to show these in neat rows of 3 items the list is
        // translated to an array of [[1, 2, 3], [4, 5, 6], ...]
        const rows = [];
        const categories = this.getMenuCategories(config.classificationId);
        if (categories) {
          let rowNr = 0;
          categories
            .forEach(c => {
              rows[rowNr] = rows[rowNr] || [];
              rows[rowNr].push(c);
              if (rows[rowNr].length === 3) {
                rowNr++;
              }
            })
        }
        return rows;
      }
    },
    methods: {
      ...mapActions([
        'getCategories',
        'getQuestionsAndAnswers'
      ]),
      setOgData() {
        const ogTitleElement = getOrCreateMetaElement('og:title');
        const ogDescElement = getOrCreateMetaElement('og:description');

        let title = 'Veelgestelde vragen : Nationale-Nederlanden';
        let desc = 'Overzicht veelgestelde vragen particulier : Nationale-Nederlanden';

        if (this.selectedMenuCategory.name && this.selectedFaqCategory.name) {
          title = this.selectedFaqCategory.name + ' - ' + this.selectedMenuCategory.name + ' - Veelgestelde vragen : Nationale-Nederlanden';
          desc = 'Antwoorden op veelgestelde vragen over ' + this.selectedFaqCategory.name + ' - ' + this.selectedMenuCategory.name + ' vindt u hier. Meer weten? Neem gerust contact met ons op.';
        }

        ogTitleElement.setAttribute('content', title);
        ogDescElement.setAttribute('content', desc);
      },
      updateCanonicalLink() {
        canonicalLinkElement.setAttribute('href', canonicalLinkOriginalUrl + window.location.hash);
      }
    }
  });
}