import template from './faqSubCategory.html';
import queryParamsMixin from "../queryParamsMixin.js";

export default {
  template,
  mixins: [queryParamsMixin],
  computed: {
    isSelected() {
      return this.category.id === this.selectedFaqCategoryId;
    }
  },
  mounted() {
    if(this.isSelected) {
      this.$emit('expand');
    }
  },
  props: ['category', 'parentId']
};
