import template from './faqAnswer.html';
import { mapActions } from 'vuex';

export default {
  template,
  props: ['faq'],
  data() {
    return {
      answer: this.faq.answer,
      loading: false,
    }
  },
  mounted() {
    if (!this.answer) {
      this.handleAnswerPromise(this.getAnswer(this.faq));
    } else {
      this.loading = false;
    }
  },
  methods: {
    ...mapActions([
      'getAnswer',
      'getAnswerByRelatedSearch'
    ]),
    handleAnswerPromise(answerPromise) {
      this.loading = true;
      answerPromise.then(answer => {
        this.answer = answer;
        this.loading = false;
      }).catch(() => {
        this.loading = false;
      });
    },
    handleAnswerClick(evt) {
      let q;
      if (evt.target.getAttribute('data-role') === 'dialog') {
        q = evt.target.innerText;
      }
      if (evt.target.getAttribute('data-role') === 'ask-link') {
        q = evt.target.getAttribute('data-ask-link-question');
      }
      if (q) {
        evt.preventDefault();
        this.answer = null;
        this.handleAnswerPromise(this.getAnswerByRelatedSearch(q));
      }
    },
    parseLists(answerString) {
      let newString;
      const matches = answerString.match(/[\r\n\s]+(\s*\*\s+[^\r\n]+)+/gm);
      if (matches && matches.length > 0) {
        for (let i = matches.length - 1; i >= 0; i--) {
          newString = matches[i];
          newString = newString.replace(/^\s+|\s+$/m, ''); // Strip spaces from beginning & end
          newString = newString.replace(/^\*\s*/, '<ul><li>'); // Replace whitespace at beginning with list start
          newString = newString.replace(/\s+\*\s+/mg, '</li><li>'); // Replace whitespace & * between strings with list end & start tags
          newString = newString.replace(/\s*$/, '</li></ul>'); // Replace whitespace at end with list end
          answerString = answerString.replace(matches[i], newString); // Replace original list string with our markup
        }
      }

      return answerString;
    },
    parseDialogs(answerString) {
      const dialogOptionsMatch = answerString.match(/%{DialogOption\(([^}]+)\)}/gi);
      if (dialogOptionsMatch) {
        dialogOptionsMatch.forEach(match => {
          const optionStr = match.replace(/%{dialogoption\(/i, '').replace(')}', '');
          answerString = answerString.replace(match, '<a class="normal" data-role="dialog" href="#">' + optionStr + '</a>');
        });
      }

      return answerString;
    },
    parseLinks(answerString, links) {
      if (!links || links.length === 0) {
        return answerString;
      }
      let placeholder;
      links.forEach(link => {
        placeholder = '%{Link(' + link.id + ')}';
        if (link.target === "ask") {
          const newQuestionString = link.url.match(/http:\/\/ask\((.+)\)/)[1];
          answerString = answerString.replace(placeholder, '<a class="normal" href="#" data-role="ask-link" data-ask-link-question="' + newQuestionString + '">' + link.text + '</a>');
        } else {
          answerString = answerString.replace(placeholder, '<a class="normal" href="' + link.url + '" target="' + link.target + '">' + link.text + '</a>');
        }
      });


      return answerString;
    },
    parseNewlines(answerString) {
      answerString = answerString.replace(/\r\n/g, '\n');
      answerString = answerString.replace(/\r/g, '\n');
      answerString = answerString.replace(/\n\n/g, '<br><br>');
      answerString = answerString.replace(/([^>])\n/g, '$1<br>');

      return answerString;
    },
    parseBoldText(needle, answerString) {
      let pattern = (needle === '*') ? /\*\*[A-Za-z0-9 .,!?{}%()]+\*\*/gi : /__[A-Za-z0-9 .,!?{}%()]+__/gi;
      let smallPattern = (needle === '*') ? /\*\*/gi : /__/gi;
      const matches = answerString.match(pattern);

      if (matches !== null && matches.length && matches.length > 0) {
        for (let index = 0; index < matches.length; index++) {
          answerString = answerString.replace(matches[index], matches[index].replace(pattern, '<b>' + matches[index] + '</b>'));
        }
      }
      return (answerString.replace(smallPattern, ""));
    },
    filterChatbotTags(answerString) {
      return answerString.replace(/{{wait}}/g, '');
    }
  },
  computed: {
    noResults() {
      return !this.answer;
    },
    answerHtml() {
      if (!this.answer) {
        return ''
      }

      let answerString = this.answer.text;
      answerString = this.filterChatbotTags(answerString);
      answerString = this.parseBoldText('*', answerString); // Need to go before parseLinks
      answerString = this.parseBoldText('_', answerString); // Need to go before parseLinks
      answerString = this.parseLists(answerString);
      answerString = this.parseLinks(answerString, this.answer.links);
      answerString = this.parseDialogs(answerString);
      answerString = this.parseNewlines(answerString);

      return answerString;
    }
  }
};
