import { moveChildNodes } from '../utility/element.helper';
import moduleconfig from "../u-moduleconfig/moduleconfig";
import stickyService from "../utility/stickyService";

const stickyColumnClass = 'sticky-column';

export default class StickyContainer {
  constructor(element) {
    this.element = element;

    // default config
    this.config = {
      offsetPx: 0
    }
  }

  init() {
    this.getConfig();
    this.getStickyContentElements();
    this.checkStickiness();
    this.checkTop();
    this.setEventHandlers();
    this.setMutationObserver();

    return this;
  }

  getConfig() {
    this.config = moduleconfig({ element: this.element, defaults: this.config });
    this.config.offsetPx = parseInt(this.config.offsetPx, 10);
  }

  setMutationObserver() {
    this.observer = new MutationObserver(mutationList => {
      mutationList.forEach(mutation => {
        if (mutation.type === 'attributes') {
          this.getConfig();
        }
      });
      this.checkTop();
      this.checkStickiness();
    });
    this.observer.observe(this.element, {
      childList: true,
      subtree: true,
      attributes: true,
      attributeFilter: ['data-config']
    });
  }

  setEventHandlers() {
    window.addEventListener('resize', this.checkStickiness.bind(this));
    window.addEventListener('scroll', this.checkTop.bind(this));
  }

  getStickyContentElements() {
    this.stickyContentElements = [];
    [...this.element.querySelectorAll('[data-role="sticky-column"]')].forEach(stickyColumn => {
      const contentElement = document.createElement('div');
      moveChildNodes(stickyColumn, contentElement);
      stickyColumn.appendChild(contentElement);
      this.stickyContentElements.push(contentElement);
    });
  }

  getOffset() {
    return this.config.offsetPx + stickyService.getTopOffset();
  }

  checkStickiness() {
    const elementRect = this.element.getBoundingClientRect();
    this.stickyContentElements.forEach(stickyContentElement => {
      const columnRect = stickyContentElement.parentNode.getBoundingClientRect();

      if (elementRect.top === columnRect.top && (stickyContentElement.getBoundingClientRect().height + this.getOffset()) < window.innerHeight) {
        stickyContentElement.classList.add(stickyColumnClass);
      } else {
        stickyContentElement.classList.remove(stickyColumnClass);
      }
    });
  }

  checkTop() {
    if (this.stickyContentElements.length === 0) {
      return;
    }

    const elementRect = this.element.getBoundingClientRect();
    const contentRect = this.stickyContentElements[0].getBoundingClientRect();

    if (elementRect.top - this.getOffset() < 0) {
      const maxTop = Math.max(elementRect.height - contentRect.height, 0);
      this.setTop(Math.min(maxTop, elementRect.top * -1 + this.getOffset()));
    } else {
      this.setTop(0);
    }
  }

  setTop(top) {
    this.stickyContentElements.forEach(stickyContentElement => {
      stickyContentElement.style.top = top + 'px';
    })
  }
}