import http from '../../app-utilities/http.js';
import { buildQueryString } from '@nn/psdl-utility/src/query-string.js';

// Initial settings
const cx = {
  url: null,
  classificationId: null,
  apiKey: null,
  culture: null
};
const session = {
  id: null,
  timeStamp: null
};

function config(config) {
  // server setting
  cx.url = config.cxurl;
  cx.classificationId = config.classificationId;
  cx.apiKey = config.apiKey;
  cx.culture = config.culture;
}

function checkSession(id) {
  // Some requests will include a sessionId in the response, if this sessionId is passed on
  // subsequent requests there will be no extra payment for them.
  // A session will timeout after some time (15 minutes probably), in this case the request will include a new sessionId
  // This code ensures that all subsequent requests will use this new Id
  // if a request contained no sessionId then the previous sessionId will be preserved
  if (id && session.id !== id) {
    session.id = id;
  }
}

function handleSessionResponse(result) {
  checkSession(result.sessionId);
  return Promise.resolve(result);
}

function getQueryString(params) {
  const queryParams = {
    apiKey: cx.apiKey,
    culture: cx.culture
  };
  if (session.id) {
    queryParams['session.id'] = session.id;
  }

  Object.assign(queryParams, params, window.app.blueconic.Connector.cxGetProfileValues());
  return '?' + buildQueryString(queryParams);
}

function validateRequiredProperty(property, message) {
  if (!property) {
    throw new Error('Missing required property: ' + message)
  }
}

function getCategories(classificationId, categoryId) {
  classificationId = classificationId || cx.classificationId;
  validateRequiredProperty(classificationId, '[classificationId] in httpService.getCategories');
  validateRequiredProperty(categoryId, '[categoryId] in httpService.getCategories');

  return http.get(cx.url + '/classifications/' + classificationId + '/categories/' + categoryId + getQueryString());
}

function getFaqs(classificationId, categoryId, parameters) {
  classificationId = classificationId || cx.classificationId;
  validateRequiredProperty(classificationId, '[classificationId] in httpService.getFaqs');
  validateRequiredProperty(categoryId, '[categoryId] in httpService.getFaqs');

  return http.get(cx.url + '/classifications/' + classificationId + '/categories/' + categoryId + '/faqs' + getQueryString(parameters))
    .then(handleSessionResponse);
}

function getFaqsByIds(ids) {
  validateRequiredProperty(ids, '[ids] in httpService.getFaqsByIds');
  return http.get(cx.url + '/faqs/kbas' + getQueryString({ids}))
    .then(handleSessionResponse);
}

function getAnswerByRelatedSearch(q) {
  validateRequiredProperty(q, '[q] in httpService.getAnswerByRelatedSearch');
  return http.get(cx.url + '/ask' + getQueryString({q}))
    .then(handleSessionResponse);
}

function getAnswer(faqId) {
  validateRequiredProperty(faqId, '[faqId] in httpService.getAnswer');
  return http.get(cx.url + '/faqs/' + faqId + '/output' + getQueryString())
    .then(handleSessionResponse);
}

function sendFeedback(feedback) {
  validateRequiredProperty(feedback, '[feedback] in httpService.sendFeedback');
  return http.post(cx.url + '/feedback/' + getQueryString(), feedback);
}

export default {
  cx,
  session,
  config,
  checkSession,
  getQueryString,
  getCategories,
  getFaqs,
  getFaqsByIds,
  getAnswerByRelatedSearch,
  getAnswer,
  sendFeedback
};