import Vue from "vue";
import VueComponents from '@nn/vue-components';
import { mapActions } from 'vuex';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';
import { store } from "./store.js";
import './components';
import '../app-vue-shared/shared.js';

import template from './faqQuestionList.html';
import httpService from "./http/httpService";

Vue.use(VueComponents);

export default function faqCategoryList(root, config) {
  httpService.config(config);

  return new Vue({
    el: createVueChildContainer(root),
    template,
    store,
    data: {
      faqs: [],
      modal: {
        faq: {}
      },
      loading: true,
    },
    mounted() {
      let faqPromise;
      if (config.questionIds) {
        faqPromise = this.getFaqsByIds(config.questionIds);
      } else {
        faqPromise = this.getFaqs({
          classificationId: config.classificationId,
          categoryId: config.categoryId,
          params: {
            max: config.max
          }
        });
      }
      faqPromise
        .then(faqs => this.setFaqs(faqs))
        .catch(() => {
          this.loading = false;
        });
    },
    computed: {
      noResults() {
        return !this.faqs || this.faqs.length === 0;
      }
    },
    methods: {
      ...mapActions([
        'getFaqsByIds',
        'getFaqs',
        'getAnswer'
      ]),
      setFaqs(faqs) {
        this.faqs = faqs;
      },
      showAnswer(faq) {
        this.modal = {faq};
        this.$refs.faqQuestionModal.open();
      },
      closeDialog() {
        this.$refs.faqQuestionModal.close();
      }
    }
  });
}
