import Vue from "vue";
import { createVueChildContainer } from '../../app-vue-shared/createVueChildContainer';
import { store } from "../store.js";
import '../components';

export default function sscQuestionList (root) {
  return new Vue({
    el: createVueChildContainer(root),
    template: '<faq-questions></faq-questions>',
    store,
    mounted() {
      this.$store.state.legacy = true;
    }
  });
}