import template from './faqFeedback.html';
import { mapActions } from 'vuex';

export default {
  template,
  props: ['answer'],
  data () {
    return {
      feedbackDone: false,
      commentVisible: false,
      comment: ''
    }
  },
  methods: {
    ...mapActions([
      'sendFeedback'
    ]),
    showComment () {
      this.commentVisible = true;
    },
    send (isPositive) {
      this.feedbackDone = true;
      const feedback = {
        originInteractionId: this.answer.interactionId,
        score: isPositive ? '1' : '0',
        label: isPositive ? 'yes' : 'no',
        comment: this.comment
      };
      this.sendFeedback(feedback);

      // Publish to analytics
      window.app.analytics.publish('element-tracking', 'feedback-element', {
        'event_action': 'click',
        'event_label': this.answer.faqQuestion,
        'id': this.answer.interactionId,
        'helpful': feedback.label,
        'comment': feedback.comment
      });
    }
  }
};
