export default {
  data() {
    return {
      selectedMenuCategoryId: null,
      selectedFaqCategoryId: null
    }
  },
  mounted() {
    this.updateSelectionQueryParameters();
    window.addEventListener("hashchange", this.updateSelectionQueryParameters, false);
  },
  beforeDestroy() {
    window.removeEventListener("hashchange", this.updateSelectionQueryParameters, false);
  },
  methods: {
    getSelectionQueryParameters() {
      const query = window.location.search || window.location.hash;
      if (!query) {
        return {};
      }

      window.history.replaceState(window.history.state, document.title, window.location.pathname + '#' + query.replace('#', ''));

      const parts = query.split('?');
      if (parts.length !== 2) {
        return {};
      }

      const parameters = parts[1].split('&');
      const params = {};
      parameters.forEach(param => {
        const item = param.split('=');
        if (item.length !== 2 || item[0].length === 0) {
          return;
        }
        params[item[0]] = item[1];
      });
      if (
        !params.et_rsparentid || isNaN(params.et_rsparentid) ||
        !params.et_selrsid || isNaN(params.et_selrsid)
      ) {
        window.location.hash = '#';
        return {};
      }

      return {
        selectedMenuCategoryId: parseInt(params.et_rsparentid),
        selectedFaqCategoryId: parseInt(params.et_selrsid)
      };
    },
    updateSelectionQueryParameters() {
      const params = this.getSelectionQueryParameters();
      this.selectedMenuCategoryId = params.selectedMenuCategoryId;
      this.selectedFaqCategoryId = params.selectedFaqCategoryId;
    }
  }
}
