import Vue from "vue";
import Vuex from 'vuex';
import httpService from './http/httpService.js';

Vue.use(Vuex);

/*
* DATA MODEL
*
* Category: {
*   id: integer,
*   name: string,
*   subCategories: Category[],
*   faqs: Faq[]
* }
*
* Faq: {
*   id: integer,
*   question: string,
*   answer: Answer
* }
*
* Answer: {
*   faqQuestion: string
*   interactionId: uuid,
*   dialogOptions: [],
*   links: [],
*   text: string,
*   type: string
* }
*/

function mapAnswer(answer) {
  return {
    faqQuestion: answer.outputs[0].faqQuestion,
    interactionId: answer.interactionId,
    dialogOptions: answer.outputs[0].outputParts[0].dialogOptions,
    links: answer.outputs[0].outputParts[0].links,
    text: answer.outputs[0].outputParts[0].text,
    type: answer.outputs[0].outputParts[0].type,
  }
}

function mapFaq(faq) {
  return {
    id: faq.id,
    question: faq.question
  }
}

function mapCategory(category) {
  return {
    id: category.id,
    name: category.name,
    subCategories: mapSubCategories(category.subCategories)
  }
}

function mapSubCategories(subCategories) {
  return subCategories.map(mapCategory)
}

function findCategoryById(categories, id) {
  for (let i = 0; i < categories.length; i++) {
    if (categories[i].id === id) {
      return categories[i];
    }
    let found = findCategoryById(categories[i].subCategories, id);
    if (found) {
      return found;
    }
  }
}

const state = {
  categories: {
    // stored per classificationId
  }
};

const mutations = {
  addCategories(state, {classificationId, category}) {
    // the subCategories from the root category are menu categories, those without nested categories are
    // therefore filtered out here, we don't need them
    category.subCategories = category.subCategories.filter(c => c.subCategories.length > 0);

    state.categories[classificationId] = mapCategory(category);

    // this statement ensures reactivity for Vue
    state.categories = {...state.categories};
  }
};

const getters = {
  getMenuCategories: (state) => (classificationId) => {
    if (!state.categories[classificationId]) {
      return [];
    }
    return state.categories[classificationId].subCategories;
  },
  getCategoryById: (state) => (classificationId, id) => {
    if (!state.categories[classificationId]) {
      return {};
    }
    return findCategoryById(state.categories[classificationId].subCategories, id) || {};
  },
};

const actions = {
  getCategories({commit, state}, {classificationId, categoryId}) {
    if (state.categories[classificationId]) {
      return Promise.resolve();
    }
    return httpService.getCategories(classificationId, categoryId)
      .then(result => {
        commit('addCategories', {classificationId, category: result});
      });
  },
  getFaqs({getters}, {classificationId, categoryId, params}) {
    const category = getters.getCategoryById(classificationId, categoryId);
    if(category && category.faqs) {
      return Promise.resolve(category.faqs);
    }

    return httpService.getFaqs(classificationId, categoryId, params).then(response => {
      const faqs = response.result.map(mapFaq);
      if (category) {
        category.faqs = faqs;
      }
      return faqs;
    });
  },
  getFaqsByIds(context, ids) {
    return httpService.getFaqsByIds(ids).then(response => {
      // filter out not found ID's
      return response.result.filter(faq => !!faq).map(mapFaq);
    });
  },
  getAnswerByRelatedSearch(context, q) {
    return httpService.getAnswerByRelatedSearch(q).then(response => {
      return mapAnswer(response);
    });
  },
  getAnswer(context, faq) {
    return httpService.getAnswer(faq.id).then(response => {
      const answer = mapAnswer(response);
      if(answer) {
        faq.answer = answer;
      }
      return answer;
    });
  },
  sendFeedback(context, feedback) {
    return httpService.sendFeedback(feedback);
  }
};

const store = new Vuex.Store({
  state,
  mutations,
  getters,
  actions
});

export { store, state, mutations, getters, actions }
