import template from './faqCategoryExpander.html';

export default {
  template,
  data() {
    return {
      isExpanded: false
    }
  },
  props: ['category', 'parentId'],
  methods: {
    expand() {
      this.isExpanded = true;
      this.$emit('expand');
    },
    toggleExpanded() {
      this.isExpanded = !this.isExpanded;
      window._al.trackers.accordeon.publish({
        event_action: this.isExpanded ? 'open': 'close',
        event_label: this.category.name
      })
    }
  }
};
