import { mapGetters, mapActions } from 'vuex';
import template from './faqQuestions.html';
import queryParamsMixin from "../queryParamsMixin";

export default {
  template,
  mixins: [queryParamsMixin],
  props: ['classificationId'],
  data() {
    return {
      faqs: [],
      loading: true,
    }
  },
  methods: {
    ...mapActions([
      'getFaqs'
    ]),
    updateFaqs() {
      this.faqs = [];
      this.loading = true;
      this.getFaqs({
        classificationId: this.classificationId,
        categoryId: this.selectedFaqCategoryId
      }).then(faqs => {
        this.faqs = faqs;
        this.loading = false;
      }).catch(() => {
        this.loading = false;
      })
    }
  },
  mounted() {
    this.updateFaqs();
  },
  watch: {
    selectedFaqCategoryId() {
      this.updateFaqs();
    }
  },
  computed: {
    ...mapGetters([
      'getCategoryById'
    ]),
    noResults() {
      return this.faqs.length === 0;
    },
    selectedFaqCategory() {
      return this.getCategoryById(this.classificationId, this.selectedFaqCategoryId);
    }
  }
};
